import fetch from '../common/fetch'

export function login(data) {
    return fetch({
        url: "login",
        method: "post",
        data
    })
}
export function search(data) {
    return fetch({
        url: "_search",
        method: "post",
        data
    })
}
export function signup(data) {
    return fetch({
        url: "signup",
        method: "post",
        data
    })
}


